<template>

    <div class="agreement-sms-confirmation">

        <form @submit.prevent="confirmSuccess"
              class="agreement-code-form">

            <div class="agreement-signing-title">СМС подтверждение</div>

            <phone-code-group @isFilled="checkFilled"/>

            <p>Мы отправили код для подтверждения <br>на закрепленный номер <span>{{person.phone | VMask('+# ### ###-##-##')}}</span></p>

            <div class="form-submit">
                <button :disabled="!isFilled || load"
                        :class="load ? 'loading' : ''"
                        class="btn btn-sm btn-block">Отправить
                </button>
            </div>

        </form>

    </div>

</template>

<script>

    import PhoneCodeGroup from "@/components/Inputs/PhoneCodeGroup";
    import {mapState} from "vuex";

    export default {
        name: "AgreementConfirmation",
        components: {
            PhoneCodeGroup
        },
        computed: {
            ...mapState({
                person: state => state.agreement.person
            })
        },
        data: () => ({
            load: false,
            isFilled: false,
            getParametr: '',
            code: ''
        }),
        methods: {
            checkFilled(bool, value) {
                this.isFilled = bool
                this.code = parseInt(value)
            },
            confirmSuccess() {
                if ( this.isFilled ) {
                    this.load = true
                    this.$store.dispatch('getSubscribeSuccess', {token: this.getParametr, code: this.code}).then(() => {
                        this.load = false
                        this.$router.push('/agreement/result')
                    }).catch(this.load = false)
                }
                else {
                    alert('Введите корректный код')
                }
            }
        },
        mounted() {
            const params = window
                .location
                .search
                .replace('?','')
                .split('&')
                .reduce(
                    function(p,e){
                        const a = e.split('=');
                        p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                        return p;
                    },
                    {}
                );
            this.getParametr = params['c']
            this.$store.dispatch('getPersonData', this.getParametr)
        }
    }
</script>

