
<template>

    <div class="phone-code-group">

        <div class="form-group">
            <div class="code-inputs">
                <input v-for="(input, index) in phoneCode"
                       v-model="phoneCode[index]"
                       v-mask="'#'"
                       ref="codeInput"
                       @input="phoneCodeFilling(index)"
                       @click="checkPhoneCodeFilling(index)"
                       @keydown.backspace="deletePhoneCode(index, input)"
                       :key="index"
                       type="tel"
                       class="code-inputs-item"
                       :class="input.length > 0 ? 'focused' : ''">
            </div>
        </div>

    </div>

</template>

<script>
    export default {
        data() {
            return {
                phoneCode: ['', '', '', '', '', ''],
                counter: 300,
                counterSetInterval: '',
            }
        },
        methods: {
            phoneCodeFilling(index) {
                let val = this.phoneCode[index].replace(/[^+\d]/g, '');
                if (val.trim().length !== 0 && index != this.phoneCode.length - 1) {
                    this.$refs.codeInput[index + 1].focus()
                    this.phoneCode[index + 1] = ''
                }
                let isFilled = true
                this.phoneCode.forEach(code => {
                    code.trim().length == 0 ? isFilled = false : ''
                })
                this.$emit('isFilled', isFilled, this.phoneCode.join(''))
            },
            checkPhoneCodeFilling(i) {
                let empty = null;
                for (let index = 0; index < this.phoneCode.length; index++) {
                    let value = this.phoneCode[index];
                    if (index < i && value.trim().length == 0) {
                        empty = index
                        break
                    }
                }
                if (empty != null && empty >= 0) {
                    this.$refs.codeInput[empty].focus()
                }
            },
            deletePhoneCode(index, value) {
                if (value.trim().length == 0 && index >= 1) {
                    this.$refs.codeInput[index - 1].focus()
                }
            },
        },
        mounted() {
            this.$emit('isFilled', false, this.phoneCode.join(''))
        }
    }
</script>
